@import "global/01-mixins";
@import "fonts";
@import "global/03-vars";
html {
    box-sizing: border-box;
}

body {
    font-family: 'Gilroy', sans-serif;
    font-size: 20px;
    line-height: 1;
    font-weight: 500;
    color: #3A3A3A;
    position: relative;
    height: 100%;
    background: #fff;
    -webkit-tap-highlight-color: transparent;
}

div,
section,
footer,
.btn {
    box-sizing: border-box;
}

a {
    color: #121110;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

a:focus,
input:focus {
    outline: none;
}

p {
    color: #252525;
    font-size: 20px;
    font-weight: 400;
    line-height: 29px;
    margin: 0 0 10px 0;
}

input {
    display: block;
    width: 100%;
    font-size: 30px;
    font-weight: 700;
    line-height: 29px;
    padding: 0 8px;
    margin-bottom: 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

input:hover,
input:focus {
    outline: none;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

ul li {
    padding: 0 0 0 0;
}

button {
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

.title {
    color: #252525;
    font-size: 42px;
    font-weight: 700;
    line-height: 53px;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.desc {
    color: #252525;
    font-size: 20px;
    font-weight: 400;
    line-height: 29px;
    margin-bottom: 15px;
}

img {
    max-width: 100%;
    max-height: 100%;
    display: block;
}

.clearfix:after {
    content: '';
    display: table;
    clear: both;
}

.wrapper {
    position: relative;
    min-height: 150vh;
}

.container {
    position: relative;
    max-width: 1334px;
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}


/* I E   S T Y L E S */

@media all and (-ms-high-contrast: none),
(-ms-high-contrast: active) {
    .block {
        width: 100%;
    }
}

#hamburger-icon {
    position: relative;
    display: none;
    width: 18px;
    height: 14px;
}

#hamburger-icon .line {
    position: absolute;
    left: 0;
    display: block;
    background: #ff6543;
    width: 18px;
    height: 2px;
    transition: .4s;
    -webkit-transition: .4s;
    -moz-transition: .4s;
}

.mobile-menu {
    display: none;
}

.line.line-1 {
    top: 0;
}

.line.line-3 {
    top: 100%;
}

.line.line-2 {
    top: 50%;
}

#hamburger-icon.active .line-1 {
    transform: translateY(7px) translateX(0) rotate(45deg);
    -webkit-transform: translateY(7px) translateX(0) rotate(45deg);
    -moz-transform: translateY(7px) translateX(0) rotate(45deg);
    -ms-transform: translateY(7px) translateX(0) rotate(45deg);
    -o-transform: translateY(7px) translateX(0) rotate(45deg);
}

#hamburger-icon.active .line-2 {
    opacity: 0;
}

#hamburger-icon.active .line-3 {
    transform: translateY(-7px) translateX(0) rotate(-45deg);
    -webkit-transform: translateY(-7px) translateX(0) rotate(-45deg);
    -moz-transform: translateY(-7px) translateX(0) rotate(-45deg);
    -ms-transform: translateY(-7px) translateX(0) rotate(-45deg);
    -o-transform: translateY(-7px) translateX(0) rotate(-45deg);
}

.ov-hidden {
    overflow: hidden;
}

.fade {
    -webkit-transition: opacity .15s linear;
    transition: opacity .15s linear
}

.fade:not(.show) {
    opacity: 0
}

.modal-dialog {
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    max-width: 655px;
    position: relative;
    width: auto;
    height: 100vh;
    pointer-events: none
}

.modal-content {
    background: #fff;
    position: relative;
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    max-width: 655px;
    width: 100%;
    pointer-events: auto;
    border: none;
    outline: 0
}

.close:not(:disabled):not(.disabled) {
    cursor: pointer
}

.modal-open .modal {
    overflow-x: hidden;
    overflow-y: auto
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    display: none;
    width: 100%;
    height: 100%;
    overflow: hidden;
    outline: 0;
    background: rgba(10, 22, 30, 0.9);
}

.modal.fade .modal-dialog {
    -webkit-transition: -webkit-transform .3s ease-out;
    transition: -webkit-transform .3s ease-out;
    transition: transform .3s ease-out;
    transition: transform .3s ease-out, -webkit-transform .3s ease-out;
    -webkit-transform: translate(0, -50px);
    transform: translate(0, -50px);
    -webkit-transition: transform .3s ease-out;
    -moz-transition: transform .3s ease-out;
    -ms-transition: transform .3s ease-out;
    -o-transition: transform .3s ease-out
}

.modal.fade .modal-dialog {
    -webkit-transition: -webkit-transform .3s ease-out;
    transition: -webkit-transform .3s ease-out;
    transition: transform .3s ease-out;
    transition: transform .3s ease-out, -webkit-transform .3s ease-out;
    -webkit-transform: translate(0, -50px);
    transform: translate(0, -50px);
    -webkit-transition: transform .3s ease-out;
    -moz-transition: transform .3s ease-out;
    -ms-transition: transform .3s ease-out;
    -o-transition: transform .3s ease-out
}

.modal.show .modal-dialog {
    -webkit-transform: none;
    transform: none
}

.modal-close {
    position: absolute;
    width: 21px;
    height: 21px;
    background: url(../img/close.svg) no-repeat center;
    right: 31px;
    top: 31px;
    font-size: 25px;
    font-weight: 400;
    text-shadow: none;
    cursor: pointer;
    -webkit-transition: all .3s;
    transition: all .3s;
    opacity: 1
}

.form-popup {
    padding: 45px 45px 60px 45px;
}

.modal-open {
    overflow: hidden;
    padding-right: 17px!important;
}

.header-top {
    padding: 7px 0 7px 0;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.header-top__left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    span {
        display: block;
        margin-right: 66px;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #121110;
    }
}

.address {
    padding: 0 0 0 21px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #121110;
    background: url(../img/gps.svg) no-repeat left center;
    margin-right: 37px;
}

.worktime {
    padding: 0 0 0 25px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #121110;
    background: url(../img/worktime.svg) no-repeat left center;
}

.header-top__right {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    a {
        margin-right: 15px;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #121110;
        text-decoration: underline;
        &:last-child {
            margin-right: 0;
        }
    }
}

.logo {
    span {
        display: block;
        font-size: 54px;
        font-family: 'SnellRoundhand', sans-serif;
        margin-bottom: 7px;
    }
    i {
        display: block;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        color: #121110;
        line-height: 19px;
    }
}

.top-img {
    position: absolute;
    top: 31px;
    right: 0;
    left: 0;
    height: 0;
    padding-bottom: 31.62%;
    img {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}

.header-info {
    padding: 10px 0 18px 0;
    .container {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
}

.slogan {
    margin-top: 13px;
    position: relative;
    font-family: 'SnellRoundhand', sans-serif;
    font-weight: 500;
    font-size: 25px;
    line-height: 25px;
    color: #121110;
    margin-left: 75px;
    margin-right: 40px;
    &::before {
        content: '';
        display: block;
        height: 77px;
        width: 1px;
        background: #E89F71;
        position: absolute;
        left: -31px;
        top: -28px;
    }
}

.phones {
    margin-top: 14px;
    a {
        display: block;
        padding: 0 0 0 25px;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #121110;
        background: url(../img/tel-ico.svg) no-repeat left center;
        margin-bottom: 10px;
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.header-bottom {
    position: relative;
    background: #fff;
}

.menu {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    li {
        position: relative;
    }
    a {
        display: block;
        padding: 14px 15px 15px 15px;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #121110;
    }
}

.angel-img {
    position: absolute;
    right: 154px;
    top: 20px;
    z-index: 1;
}

.cloud-left {
    position: absolute;
    left: -8px;
    top: 311px;
    z-index: 0;
}

.cloud-right {
    position: absolute;
    right: 0;
    top: 278px;
    z-index: 2;
}

.sub-menu {
    position: absolute;
    z-index: 1;
    overflow: hidden;
    max-height: 0;
    background: #000;
    min-width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(41, 41, 42, 0.06);
    transition: .3s;
    border-radius: 0 0 2px 2px;
    -webkit-border-radius: 0 0 2px 2px;
    -moz-border-radius: 0 0 2px 2px;
    -ms-border-radius: 0 0 2px 2px;
    -o-border-radius: 0 0 2px 2px;
    a {
        padding: 8px 15px 9px 15px;
    }
}

.top-slider {
    margin-left: 74px;
    max-width: 523px;
    width: 100%;
    .slick-prev,
    .slick-next {
        width: 48px;
        height: 48px;
        background: #E89F71;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        z-index: 2;
    }
    .slick-prev:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background: url(../img/prev-sl.svg) no-repeat center;
    }
    .slick-next:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background: url(../img/next-sl.svg) no-repeat center;
    }
    .slick-disabled {
        opacity: 0.3;
    }
    .slick-prev.slick-disabled:before,
    .slick-next.slick-disabled:before {
        opacity: 1;
    }
    .slick-dots {
        bottom: 15px;
    }
    .slick-dots li {
        position: relative;
        width: 9px;
        height: 9px;
        margin: 0 11.5px;
        @include transition;
    }
    .slick-dots li button {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 9px;
        height: 9px;
        background: #fff;
        padding: 0;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        @include transition;
    }
    .slick-dots li button:before {
        content: '';
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 22.5px;
        height: 22.5px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        border: 1px solid #E89F71;
        box-sizing: border-box;
        background: transparent;
        opacity: .7;
        opacity: 0;
        @include transition;
        transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
    }
    li.slick-active button {
        background: #E89F71;
    }
    li.slick-active button::before {
        opacity: 1;
    }
}

.top-slider__item {
    outline: none;
    max-width: 523px;
    width: 100%;
    padding: 62px 30px 63px 42px;
    background: rgba(255, 255, 255, 0.41);
    font-weight: 600;
    font-size: 30px;
    line-height: 42px;
    color: #474442;
    b {
        font-weight: 900;
    }
}

.top-wrap {
    padding-top: 44px;
    padding-bottom: 119px;
}

.top-slider>.top-slider__item:nth-child(n+2) {
    display: none;
}

.main-price {
    padding: 38px 0 0 0;
    h1 {
        margin-top: 0;
        font-weight: 900;
        font-size: 35px;
        line-height: 120%;
        text-align: center;
        color: #3A3A3A;
        margin-bottom: 49px;
    }
}

.category-items {
    position: relative;
    z-index: 10;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
}

.category-item {
    width: fit-content;
    position: relative;
}

.category-item-img {
    position: absolute;
    left: -6vw;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}

.category-item-content {
    @include transition;
    max-width: 326px;
    min-width: 235px;
    width: 100%;
    padding: 50px 20px 40px 84px;
    background: rgba(252, 248, 243, 0.9);
    border-radius: 37px 2px 2px 2px;
}

.category-item-title {
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    color: #3A3A3A;
    margin-bottom: 10px;
}

.category-item-col {
    font-weight: normal;
    font-size: 17px;
    line-height: 150%;
    color: #616161;
}

.category-item-price {
    font-weight: 800;
    font-size: 17px;
    line-height: 150%;
    color: #616161;
}

@media screen and (min-width:992px) {
    .category-item:hover .category-item-content {
        box-shadow: 0px 10px 20px rgba(41, 41, 42, 0.18);
    }
    .top-slider .slick-dots li:hover button {
        background: #E89F71;
    }
    .top-slider .slick-dots li:hover button::before {
        opacity: 1;
    }
    .menu li:hover ul.sub-menu {
        max-height: 2500px;
    }
    .menu li:hover>a {
        background: #E89F71;
        color: #fff;
    }
    .btn-orange:hover {
        background: #ec4824;
    }
    .modal-close:hover {
        opacity: 0.7;
    }
    .header-top__right a:hover {
        color: #E89F71;
    }
}


/* 
    M O B I L E
*/

@media screen and (max-width: 1200px) {
    html,
    body {
        min-width: 320px;
        width: 100%;
        overflow-x: hidden;
    }
    a:focus {
        outline: none;
    }
}

@media screen and (max-width: 1199px) {}

@media screen and (max-width: 991px) {
    #hamburger-icon {
        display: block;
    }
    .mobile-menu {
        position: fixed;
        top: 68px;
        right: 0;
        width: 100vw;
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-content: start;
        height: calc(100vh - 68px);
        z-index: 30;
        overflow: auto;
        background: rgba(21, 32, 39, 0.95);
        padding: 35px 15px 25px;
        box-sizing: border-box;
        // box-shadow: inset 0 29px 33px rgba(0, 86, 189, .1);
        transform: translate(100vw, 0);
        -webkit-transform: translate(100vw, 0);
        -moz-transform: translate(100vw, 0);
        -ms-transform: translate(100vw, 0);
        -o-transform: translate(100vw, 0);
        transition: .3s ease-in-out;
        -webkit-transition: .3s ease-in-out;
        -moz-transition: .3s ease-in-out;
        -ms-transition: all .3s ease-in-out;
        -o-transition: .3s ease-in-out;
    }
    .mobile-menu.active {
        transform: translate(0, 0);
        -webkit-transform: translate(0, 0);
        -moz-transform: translate(0, 0);
        -ms-transform: translate(0, 0);
        -o-transform: translate(0, 0);
    }
    .mobile-menu .menu {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
    }
    .mobile-menu .menu li {
        display: block;
        margin: 0 0 20px 0;
    }
    .mobile-menu .menu a {
        font-size: 24px;
    }
}

@media screen and (max-width: 767px) {}